export const ValueMixin = {
    props: {
        value: {},
    },
    watch: {
        value(val) {
            this.p_watchValue && val !== this.currentValue && (this.currentValue = val)
        },
        currentValue(val) {
            this.p_watchCurrentValue && this.$emit('input', val)
        },
    },
    data() {
        return {
            currentValue: this.value,
            p_watchValue: true,
            p_watchCurrentValue: true,
        }
    },
}

export const ValidMixin = {
    props: {
        required: {type: Boolean},                                      //
        rules: {type: Array},                                           //
        validOnInit: {type: Boolean},                                   //
    }
}

export const BoxMixin = {
    mixins: [ValueMixin, ValidMixin],
    props: {
        /*---------------------------------------box props-------------------------------------------*/
        boxType: {type: String, default: 'fill',},                      //
        boxColor: {type: String, default: 'primary'},                   //
        boxShape: {type: String, default: 'none'},                      //
        boxSize: {type: String, default: 'default'},                    //

        prefixIcon: {type: String},                                     //
        suffixIcon: {type: String},                                     //
        clearIcon: {type: String},                                      //hovertruevaluehoveringpl-closeclearIcon
        loading: {type: Boolean},                                       //loading 
        iconOnly: {type: Boolean},                                      //true

        readonly: {type: Boolean},                                      //
        disabled: {type: Boolean},                                      //disabled

        padding: {type: Boolean, default: true},                        //
        long: {type: Boolean,},                                         //
        width: {type: Number, default: 200},                            //
        hover: {type: Boolean},                                         //hover

        value: {},                                                      //
    },
}

export const MountedMixin = {
    data() {
        return {
            p_mounted: false
        }
    },
    mounted() {
        this.p_mounted = true
    },
    beforeDestroy() {
        this.p_mounted = false
    },
}

export const InputMixin = {
    props: {
        boxType: {default: 'line'},                                 //
        boxColor: {default: 'info'},                                //
        clearIcon: {default: 'none'},                               //

        replaceRegexp: {type: RegExp},                              ///[^\-0-9.]/g
        type: {type: String, default: 'text'},                      //inputtypepassword
        focusOnHover: {type: Boolean},                              //
        defaultClear: {type: Boolean, default: true},               //input
        placeholder: {type: String, default: '...'},         //
        keyboard: {type: Boolean,},                                 //backenterup
    }
}



